                  <div class="btn-contact">
                    <a
                      href="https://linkedin.com/in/bagas-afrizal/"
                      target="_blank"
                      class="mt-2"
                    >
                      <span></span>
                      <span></span>
                      <span></span>
                      <span></span>
                      Contact Me
                    </a>
                  </div>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </section>

    <section id="project" class="page-blog">
      <div class="project">
        <b-container>
          <h2>My Portfolio</h2>
          <b-row class="main-project">
            <b-col
              cols="12"
              md="6"
              lg="6"
              xl="4"
              data-aos="zoom-in"
              data-aos-duration="2000"
            >
              <div class="cards">
                <div class="card-img">
                  <VuePureLightbox
                    style="width: 300px; height: 150px"
                    thumbnail="https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Space/banner-image.png"
                    :images="imageSpace"
                  >
                  </VuePureLightbox>
                </div>
                <div class="content-card text-left">
                  <h2>SPACE POS</h2>
                  <p>
                    SPACE POS Apps is a web-based point of sale application that
