                  </p>
                  <a href="https://pos-space.netlify.app/" target="_blank"
                    >View Project</a
                  >
                </div>
              </div>
            </b-col>
            <b-col
              cols="12"
              md="6"
              lg="6"
              xl="4"
              data-aos="zoom-in"
              data-aos-duration="2000"
            >
              <div class="cards">
                <div class="card-img">
                  <VuePureLightbox
                    style="width: 300px; height: 150px"
                    thumbnail="https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Nepays/banner-img.jpg"
                    :images="imageNepays"
                  >
                  </VuePureLightbox>
                </div>
                <div class="content-card text-left">
                  <h2>Nepays Apps</h2>
                  <p>
                    Nepays Apps is a website-based payment gateway application
                    that is used to transfer money and top up balances
                    digitally. The top up feature is integrated with Midtrans.
                    This application was built with the team using Vue JS and
                    Express JS.
                  </p>
                  <a href="https://nepays.netlify.app/" target="_blank"
                    >View Project</a
                  >
                </div>
              </div>
            </b-col>
            <b-col
              cols="12"
              md="6"
              lg="6"
              xl="4"
              data-aos="zoom-in"
              data-aos-duration="2000"
            >
              <div class="cards">
                <div class="card-img">
                  <VuePureLightbox
                    style="width: 300px; height: 150px"
                    thumbnail="https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Covid-Track/smartmockups_kgsyw4x1.png"
                    :images="imageCovid"
                  >
                  </VuePureLightbox>
                </div>
                <div class="content-card text-left">
                  <h2>Covid Track</h2>
                  <p>
                    Covid Track is an application that contains information
                    about the number of cases of Covid-19 sufferers around the
                    world. Always update data for all countries every day. This
                    application is built using React JS as the front-end and the
