                  </p>
                  <a href="https://pandemi-data.netlify.app/" target="_blank"
                    >View Project</a
                  >
                </div>
              </div>
            </b-col>
            <b-col
              cols="12"
              md="6"
              lg="6"
              xl="4"
              data-aos="zoom-in"
              data-aos-duration="2000"
            >
              <div class="cards">
                <div class="card-img">
                  <VuePureLightbox
                    style="width: 300px; height: 150px"
                    thumbnail="https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Badag/badag-landing.jpg"
                    :images="imageBadag"
                  >
                  </VuePureLightbox>
                </div>
                <div class="content-card text-left">
                  <h2>BADAG Apps</h2>
                  <p>
                    BADAG Apps is a website-based job portal application to find
                    jobs for job seekers and find workers. It features real-time
                    chat and a standard profile. This application was built with
