                  </p>
                  <a href="https://badag.netlify.app/" target="_blank"
                    >View Project</a
                  >
                </div>
              </div>
            </b-col>
            <b-col
              cols="12"
              md="6"
              lg="6"
              xl="4"
              data-aos="zoom-in"
              data-aos-duration="2000"
            >
              <div class="cards">
                <div class="card-img">
                  <VuePureLightbox
                    style="width: 300px; height: 150px"
                    thumbnail="https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Play-Chat/smartmockups_kggoc1f2.jpg"
