                  >
                  </VuePureLightbox>
                </div>
                <div class="content-card text-left">
                  <h2>Play Chat Apps</h2>
                  <p>
                    Play Chat application is a web-based chat application that
                    is used to interact online. Has a feature to find friends,
                    current location and can send messages in real-time. This
                    application is built using Vue JS and Express JS.
                  </p>
                  <a href="https://play-chat.netlify.app/" target="_blank"
                    >View Project</a
