                </div>
              </div>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </section>

    <section id="bottom">
      <div class="akses-top text-center">
        <a href="#top" v-smooth-scroll="{ duration: 3000 }" fixed="bottom"
          >TOP</a
        >
      </div>
    </section>
  </div>
</template>

<script>
import VuePureLightbox from 'vue-pure-lightbox'
export default {
  data() {
    return {
      typeValue: '',
      typeStatus: false,
      typeArray: ['Full Stack Developer', 'Traveller'],
      typingSpeed: 200,
      erasingSpeed: 100,
      newTextDelay: 2000,
      typeArrayIndex: 0,
      charIndex: 0,
      imageSpace: [
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Space/banner-image.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Space/LoginPage.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Space/HomePageRev.png'
      ],
      imageBadag: [
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Badag/badag-landing.jpg',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Badag/landing.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Badag/home-fix.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Badag/hire-fix.png'
      ],
      imageNepays: [
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Nepays/banner-img.jpg',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Nepays/fix-landing1.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Nepays/login1.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Nepays/home1.png'
      ],
      imageCovid: [
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Covid-Track/smartmockups_kgsyw4x1.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Covid-Track/pc.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Covid-Track/tablet.png'
      ],
      imagePlay: [
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Play-Chat/smartmockups_kggoc1f2.jpg',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Play-Chat/select.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Play-Chat/fix-chat.png',
        'https://raw.githubusercontent.com/bagasafrz7/bagasafrz7.github.io/master/img/Image/Play-Chat/fix-friend.png'
      ]
    }
  },
  methods: {
    typeText() {
      if (this.charIndex < this.typeArray[this.typeArrayIndex].length) {
        if (!this.typeStatus) this.typeStatus = true
        this.typeValue += this.typeArray[this.typeArrayIndex].charAt(
          this.charIndex
        )
        this.charIndex += 1
        setTimeout(this.typeText, this.typingSpeed)
      } else {
        this.typeStatus = false
        setTimeout(this.eraseText, this.newTextDelay)
      }
    },
    eraseText() {
      if (this.charIndex > 0) {
        if (!this.typeStatus) this.typeStatus = true
        this.typeValue = this.typeArray[this.typeArrayIndex].substring(
          0,
          this.charIndex - 1
        )
        this.charIndex -= 1
        setTimeout(this.eraseText, this.erasingSpeed)
      } else {
        this.typeStatus = false
        this.typeArrayIndex += 1
        setTimeout(this.typeText, this.typingSpeed + 1000)
        if (this.typeArrayIndex === 2) {
          this.typeArrayIndex = 0
        }
      }
    }
  },
  created() {
    setTimeout(this.typeText, this.newTextDelay + 200)
  },
  components: {
    VuePureLightbox
  }
}
</script>

<style scoped src="../assets/css/main.css"></style>
